 <template>
  <div id = "signin">
    <p class="msg">{{errMsg}}</p>
    <form @submit.prevent="submit">
        <input type="email" v-model="user.email" class="email" name="email" required="required" placeholder="">
        <input type="password" v-model="user.password" class="password" name="password" required="required" placeholder="">
        <input class="btn" type="submit" value="">
    </form> 
  </div>
</template>

<script>
export default {
  name: 'signin',
  data() {
    return {
      user: {
        email: '',
        password: ''
      },
      errMsg: '      '
    }
  },
  methods: {
    submit: function() {
      if(!this.check()) {
        return;
      } else {
        var formData = JSON.stringify(this.user);
        this.$http.post('/Login', formData)
        .then(res => {
            console.log('response is' + res.data);
            if(res.data == '0'){
              //&
              this.errMsg = "";
            } else if (res.data == '1') {
              //&
              this.errMsg = "";
            } else if (res.data == '2' && document.cookie.length > 0) {
              //
              window.location.reload();
              this.$router.push('/index');
            }
          })
        .catch(e => {
          console.log("");
          console.log(e)
        })
      }
      
    },
    check: function() {
      if (document.querySelector('.email').value === '') {
        this.errMsg = '';
        return false;
      } else if (document.querySelector('.password').value === '') {
        this.errMsg = '';
        return false;
      } else {
        this.errMsg = '  ';
        return true;
      }
    }
  }
}
</script>

<style scoped>

  form {
    display: flex;
    flex-direction:column;
    margin: 0 auto;
  }
  input {
    width: 200px;
    margin: 5px auto 5px auto;
  }

  .msg {
    color: red;
    margin-bottom: 0;
  }
  
</style>


