<template>
	<div id="profile">
		<h1></h1>
		<form>
			<div class="line">
	        	<label></label>
	        	<input type="email" class="email" v-model="user.email" value="user.email" readonly="readonly">
	      	</div>
	      	<div class="line">
	        	<label></label>
	        	<input type="text" name="nickname" class="nickname" v-model="user.nickname" value="user.nickname">
	      	</div>
	      	<div class="line">
	        	<label></label>
	        	<input type="text" name="phone" v-model="user.phone">
	      	</div>
	      	<div class="line">
	        	<label></label>
	        	<div class="per">
		          <input type="checkbox" id="Romance" value="Romance" v-model="user.pereference">
		          <label for="Romance"></label>
		          <input type="checkbox" id="Comedy" value="Comedy" v-model="user.pereference">
		          <label for="Comedy"></label>
		          <input type="checkbox" id="Adventure" value="Adventure" v-model="user.pereference">
		          <label for="Adventure"></label>
		          <input type="checkbox" id="Horror" value="Horror" v-model="user.pereference">
		          <label for="Horror"></label>
		          <input type="checkbox" id="Action" value="Action" v-model="user.pereference">
		          <label for="Action"></label>
		          <input type="checkbox" id="Others" value="Others" v-model="user.pereference">
		          <label for="Others"></label>
		        </div>
		      </div>
	      <input class="btn" type="submit" value="">
	    </form>
	</div>
</template>

<script>
export default {
	name: 'profile',
	data() {
		return {
			user: {
		        email: '',
		        password: '',
		        phone: '',
		        email: '',
		        pereference: []
		    }
		}
	},
	created: function() {
    	this.getUser();
  	},
  	methods: {
  		getUser: function() {
  			this.$http.get('/user/info')
      		.then(res => {
        		console.log("");
        		console.log(res.data);
        		this.user = res.data;
      		})
      		.catch(e => {
        		console.log("");
        		console.log(e);
      		})
  		}
  	}
}
</script>

<style>
	form {
		margin-left: 100px;
	}
	h1 {
		margin-left: 100px;
	}
	.per {
		display: inline-block;
	}
	.line {
		margin-bottom: 10px;
	}
</style>


